import * as React from 'react';
import { useDispatch, useSelector } from "react-redux"
import {useTranslation} from 'react-i18next';
//const wait = require('node:timers/promises').setTimeout;

import { connect, disconnect, walletSign, updateToken } from "redux/blockchain/blockchainActions"
import { useTheme } from '@mui/material/styles'
const axios = require('axios').default;
import CONFIG from 'config/config.json'

import Storage from 'helper/Storage'

import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import StepContent from '@mui/material/StepContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

const steps = [
  {
    label: 'Connect wallet',
    description: `Connect a metamask wallet for signing in verybox.xyz site.`,   
  },
  {
    label: 'Sign in',
    description:`
              Sign content: 
              "
              Wellcomt to verybox.xyz \n,
              Address: Your address ,
              Nonce: Your nonce .
              "
              `,             
  }
];

export default function VerticalLinearStepper() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain); 
  const theme = useTheme();
  const {t, i18n} = useTranslation()

  const [stepsButtonText, setStepsButtonText] = React.useState([
    'CONNECT',
    'SIGN IN'
  ]);

  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    console.log('activeStep', activeStep)
    if(!blockchain.account){
      connectWallet()
    }else{
      disconnectWallet()
    }
  };

  // 函数实现，参数单位 毫秒 ；
  function wait(ms) {
    return new Promise(resolve => setTimeout(() => resolve(), ms));
  };

  const checkAccount = async () => {
    let checkTimes = 0
    let maxCheckTime = 3
    do {
      await wait(500)
      console.log('checkTimes',checkTimes)    
      checkTimes++
    }
    while(
      checkTimes <= maxCheckTime && !blockchain.account
    )
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  }

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const connectWallet = async () => {
    if( activeStep == 0 ){
      const res = await dispatch(connect())
      console.log('res',res)
      if(!res.error){
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
      }
    }
  }; 

  const disconnectWallet = async (event) => {
    dispatch(disconnect());
  };

  const checkUser = async () => {
    let data = {'username': blockchain.account.slice(2)};
    const nonce = await axios.post(`${CONFIG.USERLINK}checkUser`,data)
    .then(result => {
      console.log('result.data.result',result.data.result)      
      return result.data.result
    })
    return nonce    
  }

  const sign = async (event) => {
    if(blockchain.token){
      let data = {'address': blockchain.account.slice(2),'s': blockchain.token};
      axios.post(`${CONFIG.USERLINK}web3loginout`,data)
      .then(result=>{
        if(result.data.statu == 1 && result.data.message.indexOf('success') >0){
          console.log(result.data.result);
          if(result.data.result){
            dispatch(updateToken({ token:null }));
            Storage.set('token',null)          
          }else{
            alert(result.data.message)
          }         
        }
            
      })
    }else{
      const nonce = await checkUser()
      console.log('nonce',nonce)
      console.log("Wellcomt to verybox.xyz,\n"
        + "Address: "+ blockchain.account +" ,\n"
        + "Nonce: "+ nonce +" .")
      const signature = await dispatch(walletSign(
        "Wellcomt to verybox.xyz,\n"
        + "Address: "+ blockchain.account +" ,\n"
        + "Nonce: "+ nonce +" ."
      ));

      let data = {'address': blockchain.account.slice(2),'sn': signature.result.slice(2)};
      axios.post(`${CONFIG.USERLINK}web3login`,data)
      .then(result=>{
        if(result.data.statu == 1 && result.data.message.indexOf('success') >0){
          console.log(result.data.result);
          if(result.data.result){
            dispatch(updateToken({ token:result.data.result }));
            Storage.set('token',result.data.result)
            //后面每小时检验一次token
          }else{
            alert(result.data.message)
          }         
        }
            
      })
      //console.log('signature',signature)      
    }
  }; 
  
  return (
    <Box sx={{ maxWidth: 400 }}>
      <Stepper activeStep={activeStep} orientation="vertical">

          <Step key={steps[0].label}>
            <StepLabel
              optional={ null }
            >
              {steps[0].label}
            </StepLabel>
            <StepContent>
              <Typography>{steps[0].description}</Typography>
              <Box sx={{ mb: 1 }}>
                <div>
                  <Button
                    variant="contained"
                    onClick={handleNext}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    {blockchain.account ? 'DISCONNECT' :  'CONNECT'}
                  </Button>
                  <Button
                    disabled= {true}
                    onClick={handleBack}
                    sx={{ mt: 0, mr: 1 }}
                  >
                    {blockchain.account ? blockchain.account : "" }
                  </Button>
                </div>
              </Box>
            </StepContent>
          </Step>

          <Step key={steps[1].label}>
            <StepLabel
              optional={ null }
            >
              {steps[1].label}
            </StepLabel>
            <StepContent>
              <Typography>{steps[1].description}</Typography>
              <Box sx={{ mb: 2 }}>
                <div>
                  <Button
                    variant="contained"
                    onClick={sign}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    {(blockchain.token && blockchain.account) ? 'SIGN OUT' : stepsButtonText[1]}
                  </Button>
                  
                  <Button
                    disabled= {false}
                    onClick={handleBack}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    Back
                  </Button>                                   
                </div>
              </Box>
            </StepContent>
          </Step>

      </Stepper>
    </Box>
  );
}